import {Diff} from "deep-diff";
import compact from "lodash/compact";

function explainDiff<TData>(jsonDiff: Array<Diff<TData, TData>> | undefined) {
    if (jsonDiff === undefined) {
        return "No Changes";
    }

    const textDiff: string[] = [];

    jsonDiff.forEach((e) => {
        const {key, val} = explainOne(e);
        textDiff.push(`${key} = ${JSON.stringify(val)}`);
    });

    function explainOne(e: Diff<TData, TData>) {
        let op: string;
        let val: any;
        let {path} = e;
        path = (path || []).map((p: any) => {
            switch (typeof p) {
                case "string":
                    return p;
                case "number":
                    return `[${p}]`;
                default:
                    return p;
            }
        });
        const textPath = compact(path).join("/")
            .replace(/(e?s)\/\[(\d+)]/g, "$1 #$2")
            .replace(/\/\[/g, "[");
        switch (e.kind) {
            case "A":
                const item = explainOne(e.item);
                op = `${item.key} index ${e.index}`;
                val = item.val;
                break;
            case "D":
                op = "deleted";
                val = e.lhs;
                break;
            case "E":
                op = "changed";
                val = {before: e.lhs, after: e.rhs};
                break;
            case "N":
                op = "added";
                val = e.rhs;
                break;
            default:
                throw Error(e);
        }
        const key: string = `${textPath} ${op}`;
        return {key, val};
    }
    return textDiff.join("\n");
}

export {explainDiff};
